import { SHOW_DRAWER, SHOW_LOAD } from '../constants/app'
import axios from 'axios';
import FormData from 'form-data';

export function showDrawer(show) {
    return {
        type: SHOW_DRAWER,
        payload: show
    }
}

export function showLoad(show) {
    return {
        type: SHOW_LOAD,
        payload: show
    }
}


export let getElsom= async (payload) => {
    try {
        const data = new FormData();
        data.append('wallet', payload.wallet);
        const res = await axios.post(
            'http://88.212.253.143:1000/payment/elsom/check',
            data);
        return res.data
    } catch(error) {
        console.error(error)
    }
}